import * as moment from 'moment'

import {Review} from '../../types'

interface Props {
  review: Review
}

const ReviewsItem: React.FC<Props> = (props) => {
  const RATING_MULTIPLIER = 20;
  const {review} = props;

  return <li className="reviews__item">
    <div className="reviews__user user">
      <div className="reviews__avatar-wrapper user__avatar-wrapper">
        <img className="reviews__avatar user__avatar" src={review.user.avatarUrl} width="54" height="54"
             alt="Reviews avatar"/>
      </div>
      <span className="reviews__user-name">{review.user.name}</span>
    </div>
    <div className="reviews__info">
      <div className="reviews__rating rating">
        <div className="reviews__stars rating__stars">
          <span style={{width: Math.round(review.rating) * RATING_MULTIPLIER + `%`}}/>
          <span className="visually-hidden">Rating</span>
        </div>
      </div>
      <p className="reviews__text">{review.comment}</p>
      <time className="reviews__time" dateTime={moment(review.datetime).format('YYYY-MM-DD')}>{moment(review.datetime).format('MMMM YYYY')}</time>
    </div>
  </li>
};

export default ReviewsItem
